.main .stats-table-wrapper {
  width: 100%;
}

@media screen and (width >= 576px) {
  .main:not(.shifted) .stats-table-wrapper {
    width: calc(50% - .5rem);
  }
}

@media screen and (width >= 1200px) {
  .main:not(.shifted) .stats-table-wrapper {
    width: calc(33% - .666667rem);
  }
}

@media screen and (width >= 992px) {
  .main.shifted .stats-table-wrapper {
    width: calc(50% - .5rem);
  }
}

@media screen and (width >= 1400px) {
  .main.shifted .stats-table-wrapper {
    width: calc(33% - .666667rem);
  }
}

.stats {
  flex-direction: column;
  gap: 2rem;
  margin-top: 1rem;
  display: flex;
}

.stats h2 {
  margin-top: 0;
}

.stats h3 {
  margin-bottom: .75em;
}

.stats .date-fields {
  align-items: center;
  gap: 10px;
  display: flex;
}

.stats .graph {
  min-width: min(400px, 100%);
  height: 200px;
  position: relative;
}

.stats .graph-container {
  position: relative;
}

.stats .graph-container .graph-header {
  flex-wrap: wrap;
  align-items: center;
  gap: 5px 10px;
  width: calc(100% - 10px);
  display: flex;
}

.stats .graph-container .graph-header h2, .stats .graph-container .graph-header h3, .stats .graph-container .graph-header h4 {
  flex: auto;
}

.stats .graph-container .graph-header .horizontal {
  gap: 5px 10px;
}

.stats .graph-container .graph-subheader {
  margin-bottom: 10px;
  font-size: small;
}

.stats .horizontal {
  flex-wrap: wrap;
  align-items: center;
  gap: 1rem;
  display: flex;
}

.stats .horizontal .graph-container {
  flex: 1px;
}

.stats .stats-section, .stats .stats-table {
  flex-direction: column;
  display: flex;
}

.stats .stats-table.collapsible .expand-link, .stats .stats-table.collapsible.collapsed .collapse-link, .stats .stats-table.collapsible.collapsed .row:nth-child(n+7) {
  display: none;
}

.stats .stats-table.collapsible.collapsed:has(.row:nth-child(n+7)) .expand-link {
  display: block;
}

.stats .stats-table .row, .stats .stats-table .link {
  align-items: center;
  padding: 8px;
  display: flex;
}

.stats .stats-table .row.head {
  background-color: var(--selected-row);
}

.stats .stats-table .row:nth-child(odd):not(.head) {
  background-color: var(--darkened-bg);
}

.stats .stats-table .row .key {
  flex: auto;
}

.stats .stats-table .row .value {
  text-align: end;
}

.stats .stats-table-container {
  flex-wrap: wrap;
  gap: 1rem;
  display: flex;
}
